body {
  margin: 0;
  padding: 20px;
  /* background-color: silver; */
  /* color: navy; */
  line-height: 1.3;
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", sans-serif;
  text-align: center;
  font-size: 35px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

p {
  margin-top: 0;
}

li {
  list-style: none;
}

button {
  margin: 0 10px;
  /* color: navy; */
}

input {
  margin: 0 10px;
}
